.principal-menu-wrapper {
    font-size: 17px;
    border: solid 1px rgba(161, 155, 155, 0.3);
    background-color: rgb(255, 255, 255);
    height: 100%;
    width: 390px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    display: none;
    transition: all 1s;
}

.showMenu {
    display: block;
}

.close {
    cursor: pointer;
    text-align: end;
    margin-bottom: 10px;
    margin-top: 20px;
    padding-right: 10px;
}

.close:hover {
    color: #fb1414;
}

.first-menu {
    text-align: end;
}

.nav-link {
    text-decoration: none;
    color: black;
}

.principal-menu-wrapper ul {
    list-style: none;
    border-bottom: solid 1px rgba(161, 155, 155, 0.3);
    margin-bottom: 75px;
    text-align: end;
}

.principal-menu-wrapper ul:nth-child(4) {
    border-bottom: none;

}

.principal-menu-wrapper ul:last-child {
    background-color: rgba(161, 155, 155, 0.3);
    margin-bottom: 0px;
    border-bottom: none;
}

.principal-menu-wrapper ul li {
    padding: 15px;
    cursor: pointer;
}

.principal-menu-wrapper ul li:hover {
    background-color: rgba(199, 188, 188, 0.1);
}

@media (max-width: 450px) {
    .principal-menu-wrapper {
        width: 100%;
        height: 100%;
    }

    .principal-menu-wrapper {
        font-size: 20px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
    }
}
.Rectangle {
    width: 100%;
    height: 70px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    border-bottom: solid 1px rgba(161, 155, 155, 0.3);
    font-family: Montserrat;
}

.Logo {
    width: 38px;
    height: 40px;
    object-fit: contain;
    margin-left: 1em;
    transition: all 0.4s;
    cursor: pointer;
}

.Logo:hover {
    transform: scale(1.1);
    transition: all 0.4s;
}
  
.nav-bar{
    display: flex;
    list-style: none;
    font-weight: 700;
    width: 30%;
    height: 100%;
    justify-content: space-evenly;
    align-items: center;
    margin-left: 2em;
}

.nav-bar .nav-link {
    text-decoration: none;
    color: black;
}

.nav-bar li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    cursor: pointer;
}

.nav-bar .model {
    width: 126px;
    transition: all 0.4s;
}

.nav-bar .model:hover {
    color: #fb1414;
    transition: all 0.4s;
}

.nav-bar .ficha {
    width: 150px;
    transition: all 0.4s;
}

.nav-bar .ficha:hover {
    color: #fb1414;
    transition: all 0.4s;
}

.Men {
    width: 41px;
    height: 18px;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #191919;
    display: flex;
    transition: all 0.4s;
    display: flex;
    justify-content: flex-end;
    width: 60%;
}

.Men:hover {
    color: #fb1414;
    transition: all 0.4s;
}

.Gray {
    width: 25px;
    height: 18px;
    object-fit: contain;
    cursor: pointer;
}

@media (max-width: 850px) {
    .nav-bar {
        width: 70%;
    }
}

@media (max-width: 650px) {
    .Men p {
        display: none;
    }

    .Men {
        width: 80%;

    }

    .nav-bar {
        display: none;
    }

    .Rectangle {
        height: 50px;
    }
}


