.Nada-De-menor-a-mayo {
    width: 150px;
    height: 0px;
    font-size: 10px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 3.5;
    letter-spacing: -0.1px;
    color: #191919;
    background-color: rgba(255, 255, 255, 1);
    border: solid 1px rgba(161, 155, 155, 0.3) ;
    border-radius: 16px;
    padding: 1px;
    box-shadow: 6px 10px 8px #aca5a5;
    position: absolute;
    top:30px;
    z-index: 2;
    right:0;
    transition: all 1s;
    display: none;
}

.show {
    display: block;
    height: 190px;
    transition: all 1s;
    
}

.Nada-De-menor-a-mayo ul {
    list-style: none;
   
}

.Nada-De-menor-a-mayo ul li{
    border-bottom: solid 1px rgba(161, 155, 155, 0.3);
    padding: 1px;
    cursor: pointer;
}

.Nada-De-menor-a-mayo ul li:last-child {
    border-bottom: none;
}

.Nada-De-menor-a-mayo ul li:hover {
    background-color: rgba(199, 188, 188, 0.1);
    border-radius: 10px;
}

.Nada-De-menor-a-mayo ul li:active {
    background-color: rgba(199, 188, 188, 0.8);
}

